<template>
  <div>
    <section class="nav-items" v-if="!this.show">
      <ul class="item-list">
        <li
          class="item"
          v-for="(item,index) in itemList"
          :to="item.path"
          :class="{active:currentIndex==index}"
          @click="changeSelect(index)"
          :key="index"
        >{{item.name}}</li>
      </ul>
    </section>
  
  </div>
</template>

<script>
export default {
  name: "NavItemHeader",
  data() {
    return {
      show: false,
      currentIndex: 0,
           itemList: [
        {
          name: "省总动态",
          path: "/dynamic"
        },
        {
          name: "党建",
          path: "/build"
        },
        {
          name: "领导讲话",
          path: "/talk"
        },
        {
          name: "专题活动",
          path: "/active"
        },
        {
          name: "困难帮扶",
          path: "/difficult"
        },
        {
          name: "劳动模范",
          path: "/work"
        },
        {
          name: "志愿者服务",
          path: "/volunteer"
        }
      ]
    };
  },
  methods: {
    changeSelect(index) {
      console.log(this.$router.push)
      //点击切换成选中的样式，把变量的值赋值为 index
      this.currentIndex = index;
      var _this=this
        _this.$router.push({
        path: _this.itemList[index].path
      })
    },
  },
};
</script>

<style scoped>
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.nav-items {
  display: flex;
  width: 100%;
  z-index: 10;
  border-bottom:0.07rem solid #ededed;
}
/*左侧ul列表*/
.item-list {
  flex: 1;
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  height:2.5rem;
  padding-left: 1rem;
}
/*列表中的每一项*/
.item-list > .item {
  display: inline-block;
  margin-right: 7.466667vw;
  font-size: 0.8rem;
  font-weight: 900;
  color: #000000;
  text-align: center;
}
/*如果被选中的样式*/
.item-list > .active {
  height: 1.1rem;
  border: 0.1rem solid red;
  background-color: red;
  opacity: 1;
  width: 20%;
  text-align: center;
  color: white;
  margin-top: 0.5rem;
  border-radius:1rem; 
}
</style>



